    display: none; } }

.header__bar {
  padding: 10px 0; }

.header .site-title {
  font-size: .9rem;
  font-weight: 900;
  text-transform: uppercase;
  margin-bottom: 0; }

.header .site-description,
.header .button,
.header .input-group {
  margin-bottom: 0; }

@media screen and (max-width: 39.99875em) {
  .header .site-description {
    font-size: 12px; } }

@media screen and (max-width: 63.99875em) {
  .header-bar__menu, .header-bar__search {
    order: 99;
    padding: 10px 0 0;
    border-top: 1px solid lightgray; } }

@media screen and (max-width: 63.99875em) {
  .header-bar__menu {
    margin-top: 10px; }
    .header-bar__menu + .header-bar__search {
      margin-top: 10px;
      padding-bottom: 0; } }

.header-bar__branding svg {
  width: 20px;
  height: 20px; }

.archive-intro,
.content-area,
.breadcrumbs,
.entry-prev-next,
.footer,
.footer__attribution {
  border-top: 1px solid lightgray; }

.has-sidebar .content-area > * > * > * + * {
  border-left: 1px solid lightgray; }
  @media screen and (max-width: 63.99875em) {
    .has-sidebar .content-area > * > * > * + * {
      border-left: 0; } }

.entry-meta svg {
  width: 15px;
  height: 15px;
  vertical-align: -8%;
  fill: #949494; }

.entry-title {
  -ms-word-wrap: break-word;
  word-wrap: break-word;
  padding-top: 1rem; }

.author-box {
  padding: 1rem 0;
  margin-bottom: 1rem;
  border-top: 1px solid lightgray;
  border-bottom: 1px solid lightgray; }
  .author-box__headline svg {
    width: 30px;
    height: 30px;
    vertical-align: -8%; }
    @media screen and (max-width: 39.99875em) {
      .author-box__headline svg {
        width: 20px;
        height: 20px; } }
  .author-box__website svg {
    width: 12px;
    height: 12px;
    vertical-align: bottom;
    fill: #1779ba; }

.author .author-box {
  margin-bottom: 0;
  border-bottom: none; }

.svg {
  display: inline-block;
  vertical-align: middle; }

.archive-intro__svg, .archive-intro__title {
  display: inline-block;
  vertical-align: middle; }

.archive-intro__title {
  font-size: 30px; }

.archive-intro__svg {
  width: 50px;
  height: 50px; }
  .archive-intro__svg svg {
    width: 100%;
    height: 100%; }

.cell.auto {
  flex-basis: auto; }

.content-area {
  overflow-x: hidden; }

.entry-content .alignfull {
  margin-left: calc( -100vw / 2 + 100% / 2);
  margin-right: calc( -100vw / 2 + 100% / 2);
  max-width: 100vw;
  width: auto; }
